<template>
  <div v-if="clear_or_failer === 1" class="clear">
    <h1>CLEAR!!</h1>
  </div>
  <div v-if="clear_or_failer === 2" class="failer">
    <h1>FAILER...</h1>
  </div>
  <div class="container">
    <div class="left-pane">
      <div>
        <h3> 場 asa</h3>
      </div> 
      <div v-if="place_index !== -1" class="public_place">
        {{ place[place_index] }} 
      </div>
      <div v-if="place_index === -1">
        no card is submitted
      </div>
      <div class="my_place">
        <div class="my_name">
          <h3> YOU: {{ names[myIndex].name }} </h3>
        </div> 
        <div class="my_cards">
          <div v-for="(num, index) in names[myIndex].card" :key="index" class="card">
            {{ num }}
            <br>
            <button v-if="index === 0" class="play-button" @click="submit_card(index)">場に出す</button>
          </div>
        </div>
      </div>
    </div>
      <div class="right-pane">
        <div class="sub-pane">
          <OthersGameBlock :box_index=0 />
        </div>
        <div class="sub-pane">
          <OthersGameBlock :box_index=1 />
        </div>
        <div class="sub-pane">
          <OthersGameBlock :box_index=2 />
        </div>
      </div>
  </div>
</template>

<script setup>
import { provide, inject } from 'vue';
import OthersGameBlock from './OthersGameBlock.vue';

const names = inject('names');
const socket = inject('socket');
const place = inject('place');
const place_index = inject('place_index');
const clear_or_failer = inject('clear_or_failer');

// console.log(place);
console.log(place.value);
// console.log(place_index);
// console.log(place_index.value);
// console.log(place.value[0]);
// console.log(place.value[place_index.value]);

const my_socket = socket.id;
const myIndex = names.value.findIndex(person => person.socket_id === my_socket);

const submit_card = (index) => {
  socket.emit('submit_card', myIndex, index);
}

provide('myIndex', myIndex);

</script>


<style>
.container {
  display: flex;
  width: 100%;
  height: 90vh; /* 画面の高さ全体を使用 */
}

.left-pane {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 5; /* 左右のペインを等幅で配置 */
  border: 2px solid black; /* 枠線を設定 */
  padding: 10px; /* 内容と枠線の間の余白 */
}

.right-pane {
  flex: 2; /* 左右のペインを等幅で配置 */
  border: 2px solid black; /* 枠線を設定 */
  padding: 2px; /* 内容と枠線の間の余白 */
  display: flex;
  flex-direction: column; /* 子要素を縦に並べる */
}

.sub-pane {
  flex: 1; /* 上中下のペインを等高で配置 */
  border: 2px solid red; /* 枠線を設定 */
  margin: -1px 0; /* 上下のペインの間隔 */
  padding: 10px; /* 内容と枠線の間の余白 */
}

/* 最初と最後のsub-paneのマージンを取り除く */
.sub-pane:first-child {
  margin-top: 0;
}

.sub-pane:last-child {
  margin-bottom: 0;
}

.public_place {
  border: 2px solid black; /* 黒い枠線で囲む */
  border-radius: 5px; /* 角を少し丸める */
  padding: 10px 20px; /* 内側の余白 */
  margin: 5px 5px; /* 各カードの間隔 */
  font-size: 48px; /* 数字のサイズを大きくする */
  width: 80px; /* カードの幅 */
  height: 160px;
  text-align: center; /* テキストを中央揃えにする */
}

.my_place {
  border-top: 2px solid #000;
  display: flex;
  flex-direction: column;
}

.my_cards {
  max-width: 600px;
  padding-top: 20px;
  display: flex;
  flex-direction: row; /* 数字を横に並べる */
  overflow-x: auto;
  white-space: nowrap;
  align-items: center; /* 中央揃えにする */
  margin-top: 20px; /* 上部のコンテンツからの間隔 */
}

.card {
  flex-shrink: 0;
  border: 2px solid black; /* 黒い枠線で囲む */
  border-radius: 5px; /* 角を少し丸める */
  padding: 10px 20px; /* 内側の余白 */
  margin: 5px 5px; /* 各カードの間隔 */
  font-size: 48px; /* 数字のサイズを大きくする */
  width: 80px; /* カードの幅 */
  height: 160px;
  text-align: center; /* テキストを中央揃えにする */
}

</style>