<template>
  <nav>
    ここはr-ukyoのITOのページです
    <!-- <router-link to="/">NameEntry</router-link> -->
    <button @click="confirmNavigation">Reset Game</button>
    <!-- <router-link to="/about">About</router-link> -->
  </nav>
  <router-view/>
</template>

<script setup>
  import { provide, ref, onMounted } from 'vue';
  import io from 'socket.io-client';
  import { useRouter } from 'vue-router';

  const router = useRouter();

  let receivedData = ref([]);
  let selectedCardsNumber = ref();
  let selectedMaxNumber = ref();
  let place = ref([]);
  let place_index = ref(-1);
  let clear_or_failer = ref(0);

  // const socket = io('http://localhost:9000');
  const socket = io('http://13.114.195.113/');

  onMounted(() => {
    socket.on('enter_new_name', (names) => {
      console.log(`names: ${names}`);
      receivedData.value = names;
    });
    socket.on('disconnect_names', (names) => {
      receivedData.value = names;
    })
    socket.on('reload_names', (names) => {
      const myIndex = names.findIndex(person => person.socket_id === socket.id);
      router.push({
        name: 'waiting',
        params: {
          userInput: names[myIndex].name,
          // cards_per_1P: cards_per_1P.value,
          // maxNumber: maxNumber.value 
        } 
      });
      // resetすると，resetした人以外がエラーになる．
      // receivedData.value = [];
      selectedCardsNumber.value = 2;
      selectedMaxNumber.value = 100;
      place.value = [];
      place_index.value = -1;
      receivedData.value = names;
      console.log(receivedData.value);
    });
    socket.on('inputname_error', (new_name) => {
      console.log(`${new_name}は既に使われています．`);
      router.push('/');
    });
    socket.on('max_connecting_error', (message) => {
      console.log(`${message}`);
      router.push('/max_connecting');
    });
    socket.on('ok_connecting', (names, cards, max) =>{
      receivedData.value = names;
      selectedCardsNumber.value = cards;
      selectedMaxNumber.value = max;
    });
    socket.on('change_cards_num', (cards) => {
      selectedCardsNumber.value = cards;
    });
    socket.on('change_max_num', (num) => {
      selectedMaxNumber.value = num;
    });
    socket.on('start_game', (names, first_place) => {
      receivedData.value = names;
      place.value = first_place;
      router.push('/game');
    });
    socket.on('safe_card', (names, index) => {
      receivedData.value = names;
      place_index.value = index;
      console.log(index);
      console.log(place.value[index]);
    })
    socket.on('clear', () => {
      clear_or_failer.value = 1;
    });
    socket.on('failer', () => {
      clear_or_failer.value = 2;
    });
  });

  const confirmNavigation = () => {
    // 確認ダイアログを表示
    const confirmMove = window.confirm("本当にこのゲームを終了しますか？");
    
    // ユーザーがOKを選択した場合にのみ移動
    if (confirmMove) {
      socket.emit('move_page');
      router.push('/'); // Homeページへ移動
    }
  }
  provide('socket', socket);
  provide('names', receivedData);
  provide('selectedCardsNumber', selectedCardsNumber);
  provide('selectedMaxNumber', selectedMaxNumber);
  provide('place', place);
  provide('place_index', place_index);
  provide('clear_or_failer', clear_or_failer);
</script>


