<template>
  <p v-if="error_message">{{ error_message }}</p>
  <h2>人数が揃うのを待っています...</h2>
  <div>
    <h2>サーバに存在している人の名前:</h2>
    <p v-for="item in names" :key="item.name">{{ item.name }}</p>
  </div>
  <div>
    <h3>YOU: {{ userInput }}</h3>
    <!-- <p>{{ userInput }}</p> -->
  </div> 

  <h3>手札の枚数を変更できます．(1~10)</h3>
  <p>
    <select v-model="cards_per_1P">
      <option v-for="number in numbers" :key="number" :value="number">
        {{ number }}
      </option>
    </select>
      <button @click="confirmCardsNumber">枚数変更</button>
    選択されている手札の枚数: {{ selectedCardsNumber }}
  </p>

  <h3>手札の数値の上限値を変更できます．(10~1000)</h3>
  <p>
    <input type="number" v-model.number="maxNumber" min="10" max="1000" />
    <button @click="confirmMaxNumber">上限値変更</button>
    選択されている上限値: {{ selectedMaxNumber }}
  </p>

  <button :disabled="Object.keys(names).length !== 4" @click="startGame">Game Start</button>
</template>

<script setup>
import { inject, ref } from 'vue';
import { useRoute } from 'vue-router';

const route = useRoute();
// const router = useRouter();
const userInput = route.params.userInput;
const names = inject('names');
const socket = inject('socket');
let error_message = ref('');

const cards_per_1P = ref(null); // 初期値はnullや任意の数値
const selectedCardsNumber = inject('selectedCardsNumber');
const numbers = ref([...Array(10).keys()].map(n => n + 1));

const confirmCardsNumber = () => {
  console.log(cards_per_1P.value);
  socket.emit('change_cards_num', cards_per_1P.value);
}

// const maxNumber = inject('maxNumber');
const maxNumber = ref(null);
const selectedMaxNumber = inject('selectedMaxNumber');
const confirmMaxNumber = () => {
  console.log(maxNumber.value);
  socket.emit('change_max_num', maxNumber.value);
}

const startGame = () => {
  console.log(selectedCardsNumber.value *4);
  console.log(selectedMaxNumber.value);
  if (selectedCardsNumber.value * 4 > selectedMaxNumber.value) {
    error_message.value = 'ERROR: 総枚数が上限数よりも大きくなっています！';
  } else {
    // サーバーにスタートを通知する
    socket.emit('start_game');
    error_message.value = ''; // エラーがない場合はメッセージをクリア
  }
  console.log(error_message.value);
};
</script>

