<template>
    <p> {{ names[box_user_index].name }}</p>
    <div class="his_card">
        残り
        {{ names[box_user_index].card.length }} 枚
    </div>
</template>

<script setup>
import { inject } from 'vue';
import { defineProps } from 'vue';

const names = inject('names');
const myIndex = inject('myIndex');
const props = defineProps({
  box_index: Number
});


const calculate_index = (a,b) => {
    if (a === 0){
        return b+1;
    } else if (a === 1) {
        if (b === 0) {
            return b;
        } else {
            return b+1;
        }
    } else if (a === 2) {
        if (b === 2) {
            return b+1;
        } else {
            return b;
        }
    } else if (a === 3) {
        return b;
    }
}

const box_user_index = calculate_index(myIndex, props.box_index);
</script>

<style>
.his_card {
  display: flex;
  align-items: center; /* 中央揃えにする */
  border: 2px solid black; /* 黒い枠線で囲む */
  border-radius: 5px; /* 角を少し丸める */
  padding: 10px 20px; /* 内側の余白 */
  margin: 20px 5px; /* 各カードの間隔 */
  font-size: 24px; /* 数字のサイズを大きくする */
  width: 50px; /* カードの幅 */
  height: 100px;
  text-align: center; /* テキストを中央揃えにする */
}
</style>