<template>
  <h2>接続ページ</h2>
  下に名前を入力してください
  <div>
    <input v-model="inputText" placeholder="ここに文字を入力してください" />
    <button @click="submit">送信</button>
  </div>
  <div>
    <h2>サーバに存在している人の名前:</h2>
    <p v-for="item in names" :key="item.name">{{ item.name }}</p>
  </div>
</template>

<script setup>
import { ref, inject } from 'vue';
import { useRouter } from 'vue-router';

const socket = inject('socket');
const inputText = ref('');
const router = useRouter();
const names = inject('names');
// const cards_per_1P = inject('cards_per_1P');
// const maxNumber = inject('maxNumber');

const submit = () => {
  if (inputText.value !== '') {
    console.log(inputText.value);
    socket.emit('inputName', inputText.value)
    router.push({
      name: 'waiting',
      params: {
        userInput: inputText.value,
        // cards_per_1P: cards_per_1P.value,
        // maxNumber: maxNumber.value 
      } 
    });
    inputText.value = ''; // 送信後、入力フィールドをクリア
  }
};
</script>




